body {
  margin: 0;
  font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}
a {
  color: inherit;
  text-decoration: inherit;
}

.cover {
  width: 100%;
  height: 460px;
  position: relative;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(https://coverfiles.alphacoders.com/909/90989.jpg);
}

.cover:before {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  content: " ";
  position: absolute;
  background-image: linear-gradient(-180deg, rgba(0,0,0,0.00) 58%, rgba(0,0,0,0.32) 100%);
}
.search {
  top: 48px;
}
@media (max-width: 600px){
  .search {
    left: -48px;
    max-width: 295px;
    display: block;
  }
}
